<template>
  <div class="mitacs">
    <h3 id="head">Toronto's Summer</h3>
    <div class="mitacs-content">
      <p>In the 2025 Summer, I worked as a Mitacs Intern in York University, Toronto, following <a href="http://www.yorku.ca/sychen/">Professor Stephen Y. Chen</a>.</p>
      <p class="layer">Meta-heuristic</p>
      <p>We worked on meta-heuristic, one branch of optimization techniques. It has the following characteristics:</p>
      <ol>
        <li>Inspired by simple concepts, like <a href="https://en.wikipedia.org/wiki/Evolutionary_algorithm">Evolutionary algorithm</a>, <a href="https://arxiv.org/pdf/2201.12810">Physics-based algorithm</a>, <a href="https://www.sciencedirect.com/topics/computer-science/swarm-intelligence-algorithm">Swarm-based algorithm</a>, etc.</li>
        <li>Assume problem to be black boxes, readily applicable to different problems</li>
        <li>Optimize problems stochastically, unlike gradient-based optimization, thus it's derivation-free and can avoid local-optimal stagnation</li>
      </ol>
      <p>Meta-heuristics share a common feature, which is dividing the search process into two phrases: exploration and exploitation.</p>
      <ul>
        <li>Exploration: investigate the promising area of search place as broadly as possible</li>
        <li>Exploitation: local search capability around promising area obtained in the exploration</li>
      </ul>
      <p class="layer">Grey Wolf Optimizer</p>
        <p>In our research, we dived into the <a href="https://seyedalimirjalili.com/gwo">GWO(Grey Wolf Optimizer)</a>, a swarm intelligence 
          algorithm proposed by Seyedali Mirjalili in 2014.</p>
        <p>GWO mimics the hunting mechanism of grey wolves. The main steps of hunting are searching and encircling, corresponding with exploration and exploitation. There are wolf leaders in the group, they have 
          good knowledge about the preys. In the algorithm, it mimics the wolf leaders alpha wolf, beta wolf and delta wolf, who are the best three solutions found.
          Then, the algorithm use parameters a, A and C to control the exploration and exploitation. In the exploration stage, omega wolves go away from the wolf leaders; in the exploitation 
        stage, they go forward to the wolves leaders.</p>

        <p>In 2019, researcher Kaiping Luo argued that GWO exhibits <a href="https://www.sciencedirect.com/science/article/abs/pii/S1568494619300298">origin-biased</a>, meaning the algorithm tends to favor solutions near the coordinate origin.</p>
        <p>Our group justified the origin-biased using Rastrigin function. We can see that the setting with optimum=0 converges faster than other settings.</p>
        <div id="originBias">
          <img class="pics" src="../../components/assets/mitacs/shift_both.png" alt="shift both bound and optimum">
          <img class="pics" src="../../components/assets/mitacs/shift_optimum.png" alt="shift optimum">
        </div>
        <p>
          However, the remedy adding epsilon given by Luo didn't show enough harness on benchmarks like <a href="https://github.com/P-N-Suganthan/2022-SO-BO">CEC2022</a>. 
          Thus, we utilize the origin-biased to shift the function and restart useless omega wolves to make a new remedy. The new remedy is shown <a href="/convergence_report.pdf">faster and better convergence</a>
        on CEC2022 benchmarks. Code can be found <a href="https://github.com/qiiizhen/GWO_origin_bias">here</a>.</p>
        <p class="layer">References</p>
        <p><a class="reference" href="http://dx.doi.org/10.1016/j.advengsoft.2013.12.007">Mirjalili S. et al. (2014) Grey Wolf Optimizer. Advances in Engineering Software, 69, 46-61.</a></p>
        <p><a class="reference" href="https://doi.org/10.1016/j.asoc.2019.01.025">Luo K. et al. (2019) Enhanced grey wolf optimizer with a model for dynamically estimating the location of the prey. Applied Soft Computing, 77, 225-235.</a></p>
    </div>
